<template>
    <section class="product-part max-width2">
        <c-title>
            公司产品
            <span style="color:#999">Company product</span>
        </c-title>
        <div class="product-content">
            <div class="swiper-container" id="swiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item,$index) in list">
                        <img :src="item.img" />
                    </div>
                </div>
                <!-- Add Arrows -->
                <div class="swiper-button-next"></div>
                <div class="swiper-button-prev"></div>
    
            </div>
        </div>
    </section>
</template>

<script>
    import cTitle from '@/components/head.vue';
    
    import $ from 'n-zepto';
    import data from './news.js';
    import Swiper from 'swiper';
    import 'swiper/dist/css/swiper.min.css';
    
    
    export default {
    
        components: {
            cTitle,
    
    
        },
    
        data() {
            return {
                list: data.list
            };
        },
        mounted() {
            setTimeout(function() {
                new Swiper('#swiper', {
                    slidesPerView: 5,
                    centeredSlides: false,
                    spaceBetween: 10,
                    navigation: {
                        nextEl: '.swiper-button-next',
                        prevEl: '.swiper-button-prev',
                    }
                });
            }, 100)
        },
        methods: {
    
            loadMore() {},
            goto(str) {
    
                if (str == '#') {
                    $('body,html').scrollTop(2000);
                    return false;
                }
    
                window.location.href = str;
            }
    
        }
    };
</script>

<style scoped lang='less'>
    .product-content {
        height: 200px;
        color: #000;
        width: 1200px;
        img {
            display: block;
            width: 100%;
            height: 200px;
        }
    }
    
    .swiper-container {
        width: 100%;
        height: 200px;
    }
    
    .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        width: 200px;
        height: 200px;
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
    
    .append-buttons {
        text-align: center;
        margin-top: 20px;
    }
    
    .append-buttons a {
        display: inline-block;
        border: 1px solid #007aff;
        color: #007aff;
        text-decoration: none;
        padding: 4px 10px;
        border-radius: 4px;
        margin: 0 10px;
        font-size: 13px;
    }
</style>



