<template><section class="news-part max-width2">
    <c-title>
        公司新闻
        <span style="color:#999">Company news</span>
    </c-title>
    <div class="news-partal" v-for="(item,index) in listData">
        <div class="img-parent">
            <img src="https://lappchina.lappgroup.com/fileadmin/_migrated/media/catDetail_oelflex.jpg">
        </div>
        <div class="des-part">
            <span class="data-title"> {{item.data}} </span>
            <div class="des-content">
                <div class="des-title c-line-clamp1">
                    {{item.title}}
                </div>
                <div class="des-artil c-line-clamp2">
                    {{item.des}}
                </div>
            </div>
        </div>
    </div>
</section></template>

<script>
import cTitle from '@/components/head.vue';

import $ from 'n-zepto';
import data from './news.js';
console.log(data);
export default {

    components: {
        cTitle
    },

    data() {
        return {
            title: '业务办理',
            listData: data.list
        };
    },
    created() {},
    methods: {
        loadMore() {},
        goto(str) {

            if (str == '#') {
                $('body,html').scrollTop(2000);
                return false;
            }

            window.location.href = str;
        }

    }
};
</script>

<style scoped lang='less'>

.news-part {
     clear:both;
     overflow:hidden;
    .news-partal {
        float: left;
        width: 292px;
        margin-right: 10px;
         .des-part{
          padding-top:10px;
        }
        .img-parent {
            overflow: hidden;
            min-height: 100px;
            width:100%;
            
        }
        .des-title{
            font-size:12px;
            font-weight:bold;
            line-height:17px;
            color:#000;
        }
        .des-artil{
            font-size:12px;
            line-height:17px;
            color:#999;
            margin-top:10px;
        }
        img {
            display: block;
            width: 100%;
            min-height: 100px;
        }
        .des-content{
            margin-left:55px;
             
        }
       
        .data-title{
            float:left;
            width:35px;
            line-height:17px;
        }
    }
    .news-partal:last-child {
            margin-right: 0;
        }
}
</style>



