<template>
    <section class="carousel-box max-width">
        <el-carousel>
            <el-carousel-item v-for="item in 4" :key="item">
                <img :src='sImg' />
            </el-carousel-item>
        </el-carousel>
    </section>
</template>

<script>
    import ElCarousel from 'element-ui/lib/carousel';
    import ElCarouselElement from 'element-ui/lib/carousel-item';
    import 'element-ui/lib/theme-chalk/carousel.css';
    import 'element-ui/lib/theme-chalk/carousel-item.css';
    import sImg from '../../../assets/img/top.png';
    export default {
    
        components: {
            'el-collapse': ElCarousel,
            'el-collapse-item': ElCarouselElement
        },
    
        data() {
            return {
                sImg: sImg
            };
        },
        created() {},
        methods: {
    
            goto(id) {
                window.location.href = location.href.split('#')[0] + '#/detail?id=' + id;
    
            }
    
        }
    };
</script>

<style>
    .el-carousel__container {
        height: 450px;
    }
    
    .carousel-box {
        width: 100%;
        img {
            display: block;
            width: 100%;
            min-height: 500px;
        }
    }
    
    .el-carousel__container {}
    
    .el-carousel__item h3 {
        margin: 0;
        font-size: 18px;
        opacity: .75;
        color: #475669;
    }
    
    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }
    
    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }
</style>



